.card {
    height: 480px;
    width: 350px;
    border: 1px solid #eee;
    margin: 15px;
    border-radius: 10px;
    /* overflow: hidden; */
    background-color: #fff;
    justify-content: space-between;
    overflow: hidden;
}

.card {
    position: relative;
}

.special {
    position: absolute;
    width: 356px;
    bottom: 0px;
    left: -3px;
}
